'use strict';

var ZcashBitcoreBlockHeader = require('zcash-bitcore-lib').BlockHeader;

var CID = require('cids');

var multihashes = require('multihashes');

var multihashing = require('multihashing-async');

var waterfall = require('async/waterfall');

var ZCASH_BLOCK_HEADER_SIZE = 1487;
/**
 * @callback SerializeCallback
 * @param {?Error} error - Error if serialization failed
 * @param {?Buffer} binaryBlob - Binary Zcash block if serialization was
 *   successful
 */

/**
 * Serialize internal representation into a binary Zcash block.
 *
 * @param {ZcashBlock} dagNode - Internal representation of a Zcash block
 * @param {SerializeCallback} callback - Callback that handles the
 *   return value
 * @returns {void}
 */

var serialize = function serialize(dagNode, callback) {
  var err = null;
  var binaryBlob;

  try {
    binaryBlob = dagNode.toBuffer();
  } catch (serializeError) {
    err = serializeError;
  } finally {
    callback(err, binaryBlob);
  }
};
/**
 * @callback DeserializeCallback
 * @param {?Error} error - Error if deserialization failed
 * @param {?ZcashBlock} dagNode - Internal representation of a Zcash block
 *   if deserialization was successful
 */

/**
 * Deserialize Zcash block into the internal representation,
 *
 * @param {Buffer} binaryBlob - Binary representation of a Zcash block
 * @param {DeserializeCallback} callback - Callback that handles the
 *   return value
 * @returns {void}
 */


var deserialize = function deserialize(binaryBlob, callback) {
  if (binaryBlob.length !== ZCASH_BLOCK_HEADER_SIZE) {
    var err = new Error("Zcash block header needs to be ".concat(ZCASH_BLOCK_HEADER_SIZE, " bytes"));
    return callback(err);
  }

  var dagNode = ZcashBitcoreBlockHeader.fromBuffer(binaryBlob);
  callback(null, dagNode);
};
/**
 * @callback CidCallback
 * @param {?Error} error - Error if getting the CID failed
 * @param {?CID} cid - CID if call was successful
 */

/**
 * Get the CID of the DAG-Node.
 *
 * @param {ZcashBlock} dagNode - Internal representation of a Zcash block
 * @param {Object} [options] - Options to create the CID
 * @param {number} [options.version=1] - CID version number
 * @param {string} [options.hashAlg='dbl-sha2-256'] - Hashing algorithm
 * @param {CidCallback} callback - Callback that handles the return value
 * @returns {void}
 */


var cid = function cid(dagNode, options, callback) {
  if (typeof options === 'function') {
    callback = options;
    options = {};
  }

  options = options || {}; // avoid deadly embrace between resolver and util

  var hashAlg = options.hashAlg || require('./resolver').defaultHashAlg;

  var version = typeof options.version === 'undefined' ? 1 : options.version;
  waterfall([function (cb) {
    try {
      multihashing(dagNode.toBuffer(), hashAlg, cb);
    } catch (err) {
      cb(err);
    }
  }, function (mh, cb) {
    return cb(null, new CID(version, 'zcash-block', mh));
  }], callback);
}; // Convert a Zcash hash (as Buffer) to a CID


var hashToCid = function hashToCid(hash) {
  // avoid deadly embrace between resolver and util
  var defaultHashAlg = require('./resolver').defaultHashAlg;

  var multihash = multihashes.encode(hash, defaultHashAlg);
  var cidVersion = 1;
  var cid = new CID(cidVersion, 'zcash-block', multihash);
  return cid;
};

module.exports = {
  hashToCid: hashToCid,
  ZCASH_BLOCK_HEADER_SIZE: ZCASH_BLOCK_HEADER_SIZE,
  // Public API
  cid: cid,
  deserialize: deserialize,
  serialize: serialize
};