'use strict';

var CID = require('cids');

var multihash = require('multihashing-async'); // binary resolver


module.exports = {
  resolver: {
    multicodec: 'raw',
    defaultHashAlg: 'sha2-256',
    resolve: function resolve(binaryBlob, path, callback) {
      callback(null, {
        value: binaryBlob,
        remainderPath: ''
      });
    },
    tree: function tree(binaryBlob, options, callback) {
      if (typeof options === 'function') {
        callback = options;
      }

      callback(null, []);
    }
  },
  util: {
    deserialize: function deserialize(data, cb) {
      cb(null, data);
    },
    serialize: function serialize(data, cb) {
      cb(null, data);
    },
    cid: function cid(data, options, cb) {
      if (typeof options === 'function') {
        cb = options;
        options = {};
      }

      options = options || {};
      var hashAlg = options.hashAlg || 'sha2-256';
      var version = typeof options.version === 'undefined' ? 1 : options.version;
      multihash(data, hashAlg, function (err, mh) {
        if (err) return cb(err);
        cb(null, new CID(version, 'raw', mh));
      });
    }
  }
};